<template>
<div id="app">
  <NavBar/></NavBar>
  <main>
    <router-view></router-view>
  </main>
</div>
</template>

<script>
import NavBar from './components/NavBar';
import REQUEST from './services';

export default {
  name: 'app',
  components: {
    NavBar
  },
  created() {
    const token = localStorage.getItem('jwt');
    if (token) {
      REQUEST.setToken(token);
    }
  }
}
</script>

