<template>
<nav class="nav has-shadow">
  <div class="container">
    <div class="nav-left">
      <a class="nav-item brand" @click="$router.push({ path: '/'})">
        <icon name="pied-piper-alt" scale="1.5"></icon>
        PUREHEALTH.
      </a>
      <router-link class="nav-item is-tab hidden-small" to="/" exact>Home</router-link>
      <router-link class="nav-item is-tab hidden-small" to="/about">About</router-link>
      <router-link class="nav-item is-tab hidden-small" to="/shop">Shop</router-link>
      <router-link class="nav-item is-tab hidden-small" to="/community">Community</router-link>
      <router-link class="nav-item is-tab hidden-small" to="/contact">Contact</router-link>
    </div>
    <span class="nav-toggle" :class="{ 'is-active': navMenuActive }" @click="navMenuActive = !navMenuActive">
      <span></span>
    <span></span>
    <span></span>
    </span>
    <div class="nav-menu nav-right" :class="{ 'is-active': navMenuActive }">
      <router-link class="nav-item is-tab hidden-medium" to="/" exact>Home</router-link>
      <router-link class="nav-item is-tab hidden-medium" to="/about">About</router-link>
      <router-link class="nav-item is-tab hidden-medium" to="/shop">Shop</router-link>
      <router-link class="nav-item is-tab hidden-medium" to="/community">Community</router-link>
      <router-link class="nav-item is-tab hidden-medium" to="/contact">Contact</router-link>
      <router-link class="nav-item is-tab cart" to="/cart" :class="{ 'flash': flash}">
        <b>{{numItems}}&nbsp;</b>
        <icon name="shopping-cart"></icon>
      </router-link>
      <a class="nav-item is-tab dropdown-target" @click="toggleDropdown">{{username ? username : 'Account'}}</a>
      <div ref="dropdown" :class="className" :style="style" name="account" v-if="dropdown.visible" @click="toggleDropdown">
        <div v-if="isLoggedIn">
          <router-link class="nav-item is-tab" to="/account">
            <icon name="user"></icon>&nbsp;Account</router-link>
          <a class="nav-item is-tab" @click.prevent="logout">Logout</a>
        </div>
        <div v-else>
          <router-link class="nav-item is-tab" to="/login">Login</router-link>
          <router-link class="nav-item is-tab" to="/register">Signup</router-link>
        </div>
      </div>
    </div>
  </div>
</nav>
</template>
<script>
import {
  mapGetters
} from 'vuex';
const pointerSize = 10
const directions = {
  left: [-1, 0],
  right: [1, 0],
  top: [0, 1],
  bottom: [0, -1]
}
export default {
  name: 'NavBar',
  props: ['cart'],
  data() {
    return {
      isActive: false,
      navMenuActive: false,
      dropdown: {
        visible: false,
        positionClass: '',
        position: {
          left: '0px',
          top: '0px'
        },
        pointer: false,
        width: 180
      },
      flash: false
    }
  },
  watch: {
    'cart': function(val, oldVal) {
      this.flash = true;
      setTimeout(() => {
        this.flash = false;
      }, 300);
    }
  },
  computed: {
    username() {
      return this.$store.state.user.username;
    },
    className() {
      return ['dropdown', this.dropdown.pointer && this.dropdown.positionClass]
    },
    style() {
      return {
        width: this.dropdown.width + 'px',
        ...this.dropdown.position
      }
    },
    ...mapGetters(['isLoggedIn', 'numItems'])
  },
  methods: {
    logout() {
      this.$router.push({
        path: '/'
      });
      this.$store.dispatch('logout');
    },
    showDropdown(target, position = 'bottom') {
      let direction = directions[position];
      this.dropdown.positionClass = 'dropdown-position-' + position;
      this.dropdown.visible = true;
      this.$nextTick(() => {
        let newPosition = this.getDropdownPosition(target, this.$refs.dropdown, direction);

        this.dropdown.position = {
          left: newPosition.left - (this.dropdown.width / 4) + 'px',
          top: newPosition.top + 'px',
        }
      });
    },
    toggleDropdown(e) {
      if (this.navMenuActive) {
        return;
      } else {
        if (this.dropdown.visible) {
          this.hideDropdown();
        } else {
          this.$nextTick(() => {
            this.showDropdown(e.target, 'bottom');
          });
        }
      }
    },
    hideDropdown() {
      this.dropdown.visible = false;
    },
    getDropdownPosition(target, dropdown, direction) {
      let targetRect = target.getBoundingClientRect()
      let dropdownRect = dropdown.getBoundingClientRect()

      let shiftX = dropdownRect.width - targetRect.width
      let shiftY = 0.5 * (dropdownRect.height + targetRect.height)

      let centerX = targetRect.left - 0.5 * (dropdownRect.width - targetRect.width) + window.scrollX
      let centerY = targetRect.bottom - shiftY + window.scrollY
      let positionX = 0.5 * direction[0] * (dropdownRect.width + targetRect.width)
      let positionY = direction[1] * shiftY
      if (this.dropdown.pointer) {
        positionX += direction[0] * pointerSize
        positionY += direction[1] * pointerSize
      }
      return {
        left: centerX + positionX,
        top: centerY - positionY
      }
    }
  }
}
</script>
<style scoped>
.nav-item.is-active .fa-icon {
  color: #96d6cd;
}

.nav-toggle {
  cursor: pointer;
  display: block;
  height: 3.25rem;
  position: relative;
  width: 3.25rem;
}

.nav-toggle span {
  background-color: #4a4a4a;
  display: block;
  height: 1px;
  left: 50%;
  margin-left: -7px;
  position: absolute;
  top: 50%;
  -webkit-transition: none 86ms ease-out;
  transition: none 86ms ease-out;
  -webkit-transition-property: background, left, opacity, -webkit-transform;
  transition-property: background, left, opacity, -webkit-transform;
  transition-property: background, left, opacity, transform;
  transition-property: background, left, opacity, transform, -webkit-transform;
  width: 15px;
}

.nav-toggle span:nth-child(1) {
  margin-top: -6px;
}

.nav-toggle span:nth-child(2) {
  margin-top: -1px;
}

.nav-toggle span:nth-child(3) {
  margin-top: 4px;
}

.nav-toggle:hover {
  background-color: whitesmoke;
}

.nav-toggle.is-active span {
  background-color: #00d1b2;
}

.nav-toggle.is-active span:nth-child(1) {
  margin-left: -5px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transform-origin: left top;
  transform-origin: left top;
}

.nav-toggle.is-active span:nth-child(2) {
  opacity: 0;
}

.nav-toggle.is-active span:nth-child(3) {
  margin-left: -5px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transform-origin: left bottom;
  transform-origin: left bottom;
}

@media screen and (min-width: 769px),
print {
  .nav-toggle {
    display: none;
  }
}

.nav-item {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 0;
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  font-size: 1rem;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  line-height: 1.5;
  padding: 0.5rem 0.75rem;
}

.nav-item a {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  -ms-flex-negative: 0;
  flex-shrink: 0;
}

.nav-item img {
  max-height: 1.75rem;
}

.nav-item .tag:first-child:not(:last-child) {
  margin-right: 0.5rem;
}

.nav-item .tag:last-child:not(:first-child) {
  margin-left: 0.5rem;
}

@media screen and (max-width: 768px) {
  .nav-item {
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
  }
}

.nav-item a,
a.nav-item {
  color: #7a7a7a;
}

.nav-item a:hover,
a.nav-item:hover {
  color: #363636;
}

.nav-item a.is-active,
a.nav-item.is-active {
  color: #363636;
}

.nav-item a.is-tab,
a.nav-item.is-tab {
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
  padding-bottom: calc(0.75rem - 1px);
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: calc(0.75rem - 1px);
}

.nav-item a.is-tab:hover,
a.nav-item.is-tab:hover {
  border-bottom-color: #00d1b2;
  border-top-color: transparent;
}

.nav-item a.is-tab.is-active,
a.nav-item.is-tab.is-active {
  border-bottom: 3px solid #00d1b2;
  color: #00d1b2;
  padding-bottom: calc(0.75rem - 3px);
}

@media screen and (min-width: 1000px) {
  .nav-item a.is-brand,
  a.nav-item.is-brand {
    padding-left: 0;
  }
}

.nav-left,
.nav-right {
  -webkit-overflow-scrolling: touch;
  -webkit-box-align: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  max-width: 100%;
  /* CHANGED FROM AUTO */
  overflow: hidden;
}

@media screen and (min-width: 1192px) {
  .nav-left,
  .nav-right {
    -ms-flex-preferred-size: 0;
    flex-basis: 0;
  }
}

.nav-left {
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  white-space: nowrap;
}

.nav-right {
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
}

.nav-center {
  -webkit-box-align: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 0;
  -ms-flex-positive: 0;
  flex-grow: 0;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 768px) {
  .nav-menu.nav-right {
    background-color: white;
    box-shadow: 0 4px 7px rgba(10, 10, 10, 0.1);
    left: 0;
    display: none;
    right: 0;
    top: 100%;
    position: absolute;
  }
  .nav-menu.nav-right .nav-item {
    border-top: 1px solid rgba(219, 219, 219, 0.5);
    padding: 0.75rem;
  }
  .nav-menu.nav-right.is-active {
    display: block;
  }
}

.nav {
  -webkit-box-align: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  background-color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 3.25rem;
  position: relative;
  text-align: center;
  z-index: 10;
}

.nav>.container {
  -webkit-box-align: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 3.25rem;
  width: 100%;
}

.nav.has-shadow {
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1);
}


@media screen and (max-width: 768px) {
  .hidden-small {
    display: none !important;
  }
}

@media screen and (min-width: 769px),
print {
  .hidden-medium {
    display: none !important;
  }
}

.nav-item.brand {
  font-size: 1.25rem;
}

.dropdown {
  display: block;
  position: absolute;
  box-shadow: 0px 2px 20px 0px rgba(52, 73, 94, 0.2);
  z-index: 998;
  background-color: #fff;
  user-select: none;
}

.dropdown::before {
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  content: '';
}

.dropdown-position-bottom::before {
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #fff;
  top: -10px;
  left: calc(50% - 10px);
  filter: drop-shadow(0px -2px 2px rgba(52, 73, 94, 0.1));
}

.dropdown-position-top::before {
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #fff;
  bottom: -10px;
  left: calc(50% - 10px);
  filter: drop-shadow(0px 2px 2px rgba(52, 73, 94, 0.1));
}

.dropdown-target {
  position: relative;
}

.cart {
  transition: 0.2s;
}

.cart.flash {
  color: #1acc3b;
}
.fa-icon {
  margin: 0 4px 5px 0;
}
</style>



