import { REQUEST } from './request';

const resData = (res) => res.data;

export const setToken = (token) => REQUEST.defaults.headers.common['Authorization'] = `Token ${token}`;

export const Users = {
  all: () => REQUEST.get('/users').then(resData),
  current: () => REQUEST.get('/users/user').then(resData),
  login: (email, password) => REQUEST.post('/users/login', { user: { email, password } }).then(resData),
  register: (username, email, password) => REQUEST.post('/users', { user: { username, email, password } }).then(resData)
}



