<template>
<div class="contact section">
  <div class="container content">
    <div class="wrapper">
      <figure class="image">
        <img src="../assets/fun/swanson.jpeg" />
      </figure>
      <div class="quote-container">
        <blockquote>
          <p>{{currentQuote}}</p>
          <em>Ron Swanson</em>
          <a @click="nextQuote">Get Another</a>
        </blockquote>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import swansonQuotes from '../assets/swanson_quotes';
const initIndex = Math.floor(Math.random() * swansonQuotes.length);
export default {
  name: 'Contact',
  data() {
    return {
      quotes: swansonQuotes,
      index: initIndex
    }
  },
  computed: {
    currentQuote() {
      return this.quotes[this.index];
    }
  },
  methods: {
    prevQuote() {
      if (this.index <= 0) {
        this.index = this.quotes.length - 1;
      } else {
        this.index--;
      }
    },
    nextQuote() {
      if (this.index >= this.quotes.length - 1) {
        this.index = 0;
      } else {
        this.index++;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.image {
  width: 100%;
  height: 512px;
}

@media(min-width: 769px) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: minmax(100px, 512px);
  }
  .quote-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .image img {
    height: 100%;
    width: auto;
    margin: 0 auto;
  }
  blockquote {
    min-width: 50vw;
  }
  blockquote p {
    font-size: 1rem;
  }
  blockquote em,
  blockquote a {
    font-size: 0.8rem;
  }
}

blockquote {
  background: #fff;
  padding: 15px 20px 15px 45px;
  margin: 0 0 20px;
  position: relative;
  font-family: Georgia, serif;
  font-size: 16px;
  line-height: 1.2;
  color: #666;
  height: 256px;

  text-align: left;
  border-left: 15px solid #00d1b2;
  border-right: 2px solid #00d1b2;
  box-shadow: 2px 2px 15px #ccc;
}

blockquote p {
  font-size: 1.25rem;
  padding: 2rem;
}

blockquote em,
blockquote a {
  font-size: 1rem;
}

blockquote::before {
  content: "\201C";
  /*Unicode for Left Double Quote*/
  /*Font*/
  font-family: Georgia, serif;
  font-size: 60px;
  font-weight: bold;
  color: #999;

  /*Positioning*/
  position: absolute;
  left: 10px;
  top: 5px;
}

blockquote::after {
  /*Reset to make sure*/
  content: "";
}

blockquote a {
  text-decoration: none;
  cursor: pointer;
  color: #00d1b2;
  user-select: none;
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 14px;
}

blockquote a:hover {
  color: #666;
}

blockquote em {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 14px;
  font-style: italic;
}
</style>



