<template>
<div class="account section">
  <div class="container">
    <div class="content">
      <div class="heading-container">
        <h1>{{user.username ? user.username.charAt(0).toUpperCase() + user.username.slice(1) : 'Meatloaf'}}'s Profile</h1>
        <h4>{{catchPhrase}}</h4>
      </div>
      <div class="account-details">
        <div class="account-options">
          <figure class="image">
            <img src="../assets/orang.jpeg" />
          </figure>
        </div>
        <div class="greeting">
          <button class="button-primary" @click="submitLogout">Logout</button>
        </div>
      </div>
    </div>
  </div>
  <div class="footer">
    <h6>&copy; 2017 - {{company}}</h6>
  </div>
</div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import faker from 'faker';

const DATA = {
  catchPhrase: faker.company.catchPhrase(),
  hackerPhrase: faker.hacker.phrase(),
  email: faker.internet.email(),
  company: faker.fake('{{address.city}}, {{address.stateAbbr}}'),
}
export default {
  name: 'Account',
  data() {
    return {
      ...DATA
    }
  },
  methods: {
    submitLogout() {
      this.logout();
      this.$router.push({
        path: '/'
      });
    },
    ...mapActions(['logout'])
  },
  computed: {
    ...mapState(['user'])
  }
}
</script>
<style scoped>
.account-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.greeting {
  margin-top: 2rem;
  text-align: center;
}

.heading-container {
  text-align: center;
}

h1 {
  color: #393939;
  letter-spacing: 0.1rem;
  text-align: center;
  display: inline-block;
  margin: 0 auto;
  font-size: 1.55rem;
  padding-bottom: 0.15rem;
  border-bottom: 2px solid #1de0c3;
  margin-bottom: 8px;
  font-weight: bold;
}

h4 {
  font-size: 0.9rem;
}

.image {
  max-width: 500px;
}

.footer {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-right: 0.7rem;
}

.footer h6 {
  display: inline-block;
  font-size: 0.7rem;
}

@media(min-width: 769px) {
  h1 {
    font-size: 2.5rem;
    padding: 0 2rem 0.15rem 2rem;
  }
  h4 {
    font-size: 1.6rem;
  }
  .account-details {}

  .image,
  .greeting {}
}
</style>



