<template>
<tr class="product">
  <td>{{name}}</td>
  <td>{{price}}</td>
  <td><button @click="decrement">&minus;</button><button>{{quantity}}</button><button @click="increment">&plus;</button></td>
  <td @click="remove"><span class="delete"></span></td>
</tr>
</template>
<script>
import {
  mapActions
} from 'vuex';

export default {
  name: 'Product',
  props: ['name', 'price', 'quantity', 'id'],
  methods: {
    increment() {
      this.$store.commit('INCREMENT_PRODUCT_CART_QUANTITY', { id: this.id });
    },
    decrement() {
      this.$store.commit('DECREMENT_PRODUCT_CART_QUANTITY', { id: this.id });
    },
    remove() {
      this.$store.commit('REMOVE_PRODUCT_FROM_CART', { id: this.id });
    }
  }
}
</script>
