import * as types from '../mutationTypes';
const state = {
  added: [],
  checkoutStatus: null
}

// getters
const getters = {
  numItems: state => state.added.length,
  checkoutStatus: state => state.checkoutStatus
}

// actions
const actions = {
  checkout({ commit, state }, products) {
    const savedCartItems = [...state.added]
    commit(types.CHECKOUT_REQUEST)
    shop.buyProducts(
      products,
      () => commit(types.CHECKOUT_SUCCESS),
      () => commit(types.CHECKOUT_FAILURE, { savedCartItems })
    )
  }
}

// mutations
const mutations = {
  [types.INCREMENT_PRODUCT_CART_QUANTITY](state, { id }) {
    const record = state.added.find(p => p.id === id);
    if (record) {
      record.quantity++;
    }
  },

  [types.DECREMENT_PRODUCT_CART_QUANTITY](state, { id }) {
    const record = state.added.find(p => p.id === id);
    if (record && record.quantity > 0) {
      record.quantity--;
    }
  },

  [types.REMOVE_PRODUCT_FROM_CART](state, { id }) {
    state.added = state.added.filter(p => p.id !== id);
  },

  [types.ADD_TO_CART](state, { id }) {
    state.lastCheckout = null
    const record = state.added.find(p => p.id === id)
    if (!record) {
      state.added.push({
        id,
        quantity: 1
      })
    } else {
      record.quantity++
    }
  },

  [types.CHECKOUT_REQUEST](state) {
    // clear cart
    state.added = []
    state.checkoutStatus = null
  },

  [types.CHECKOUT_SUCCESS](state) {
    state.checkoutStatus = 'successful'
  },

  [types.CHECKOUT_FAILURE](state, { savedCartItems }) {
    // rollback to the cart saved before sending the request
    state.added = savedCartItems
    state.checkoutStatus = 'failed'
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



