import REQUEST from '../services';
import * as types from './mutationTypes';

function middleware(user) {
  window.localStorage.setItem('jwt', user.token);
  REQUEST.setToken(user.token);
}

export default {
  getUser: ({ commit }, payload) => {
    return REQUEST.Users.current()
      .then(res => {
        middleware(res.user);
        commit(types.SET_USER, res.user);
      })
      .catch(err => { throw new Error(err) });
  },
  register: ({ commit }, payload) => {
    return REQUEST.Users.register(payload.username, payload.email, payload.password)
      .then(res => {
        middleware(res.user);
        commit(types.SET_USER, res.user);
      })
      .catch(err => { throw new Error(err) });
  },
  login: ({ commit }, payload) => {
    return REQUEST.Users.login(payload.email, payload.password)
      .then(res => {
        middleware(res.user);
        commit(types.SET_USER, res.user)
      })
      .catch(err => { throw new Error(err) });
  },
  logout: ({ commit }) => {
    window.localStorage.setItem('jwt', '');
    REQUEST.setToken(null);
    commit(types.SET_USER, {});
  },
  addToCart: ({ commit }, product) => {
    if (product.inventory > 0) {
      commit(types.ADD_TO_CART, { id: product.id });
    }
  },
  decrementProductCartQuantity: ({ commit }, id) => {
    commit(types.DECREMENT_PRODUCT_CART_QUANTITY, { id });
  },
  incrementProductCartQuantity: ({ commit }, id) => {
    commit(types.INCREMENT_PRODUCT_CART_QUANTITY, { id });
  },
  removeProductFromCart: ({ commit }, id) => {
    commit(types.REMOVE_PRODUCT_FROM_CART, { id });
  }
}



