export default {
  isLoggedIn: state => {
    if (state.user && state.user.username && state.user.email) {
      return true;
    } else {
      return false;
    }
  },
  cartProducts: state => {
    return state.cart.added.map(({ id, quantity }) => {
      const product = state.products.all.find(p => p.id === id);
      return {
        id: product.id,
        name: product.name,
        price: product.price,
        quantity
      }
    })
  }
}



