<template>
<div class="product-list section">
  <div class="container">
    <div class="content">
      <div v-if="products.length">
        <h1>{{cartTotal}}</h1>
        <table class="table">
          <thead>
            <th>Name</th>
            <th>Price</th>
            <th>Quantity</th>
            <th>Remove</th>
          </thead>
          <tbody>
            <Product v-for="product in products" :key="product.id" :id="product.id" :name="product.name" :price="product.price" :quantity="product.quantity"></Product>
          </tbody>
        </table>
      </div>
      <div v-else>
        <h1>There's nothing here.</h1>
        <router-link class="button" to="/shop">Shop</router-link>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import Product from './Product';
export default {
  name: 'ProductList',
  components: {
    Product
  },
  props: ['products', 'total'],
  computed: {
    cartTotal() {
      return '$' + this.total.toFixed(2);
    }
  }
}
</script>
<style scoped>

</style>



