<template>
<div class="hello section">
  <div class="content">
    <h1 class="title"></h1>
  </div>
  <div class="container">
    <video id="sick__vid" muted autoplay loop>
       <source :src="videos.mp4" type="video/mp4" />
       <source :src="videos.webm" type="video/webm" />
     </video>
  </div>
</div>
</template>

<script>
const title_arr = 'PUREHEALTH.FITNESS'.split('');
let new_arr = [];
title_arr.forEach((item, index) => {
  const el = document.createElement('span');
  el.textContent = item;
  el.dataset.index = index;
  el.style.transition = '0.35s ease-out';
  new_arr.push(el);
});
import aerobics_mp4 from '../assets/aerobics.mp4';
import aerobics_webm from '../assets/aerobics.webm';
import Alert from './Alert';
export default {
  name: 'hello',
  components: {
    Alert
  },
  beforeRouteLeave(to, from, next) {
    window.clearInterval(this.interval);
    this.spans.forEach(span => {
      span.style.transform = `scale(1)`;
      span.style.color = '#11cf93';
    });
    next();
  },
  data() {
    return {
      videos: {
        mp4: aerobics_mp4,
        webm: aerobics_webm
      },
      spans: new_arr
    }
  },
  mounted() {
    const title = document.querySelector('.title');
    this.spans.forEach(span => title.appendChild(span));
  },
  created() {
    this.animateTitle();
  },
  methods: {
    animateTitle() {
      let hue = 0;
      let currentIndex = 0;
      let reverse = false;
      let firstIteration = true;
      let num_letters = 360 / this.spans.length;
      this.interval = window.setInterval(() => {
        if (!firstIteration && currentIndex >= 0 && currentIndex < this.spans.length) {
          let prev;
          if (reverse) {
            hue += num_letters;
            prev = this.spans[currentIndex + 1];
          } else {
            hue -= num_letters;
            prev = this.spans[currentIndex - 1];
          }
          prev.style.transform = `scale(1)`;
          prev.style.color = '#11cf93';
        }
        this.spans[currentIndex].style.transform = `scale(1.6)`;
        this.spans[currentIndex].style.color = `hsl(${hue}, 100%, 50%)`;
        if (currentIndex >= this.spans.length - 1) {
          reverse = true;
        }
        if (currentIndex <= 0) {
          reverse = false;
        }

        if (reverse) {
          currentIndex--;
        } else {
          currentIndex++;
        }
        firstIteration = false;
      }, 350);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1.title {
  font-family: 'Avenir';
  font-weight: 900;
  color: #11cf93;
  text-shadow: 0px 0px 3px rgba(80, 129, 150, 0.4);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  font-size: 3rem;
  max-width: 800px;
  margin: 2rem auto;
  user-select: none;
  outline: none;
}

@media (max-width: 769px) {
  h1.title {
    font-size: 1rem;
    letter-spacing: 0.3em;
  }
}

video#sick__vid {
  margin: 0 auto;
  display: block;
}

.content {
  text-align: center;
}
</style>



