<template>
<div class="about">
  <div class="container">
    <div class="content">
      <div class="wrapper">
        <img src="../assets/fun/all_of_them.jpeg" />
        <div class="text-content">
          <h1 class="sub-title">{{phrases[0]}}</h1>
          <p>
            {{text[0].join(' ')}}
          </p>
        </div>
      </div>
      <div class="wrapper">
        <img src="../assets/fun/for_da_homies.jpeg" />
        <div class="text-content">
          <h1 class="sub-title">{{phrases[1]}}</h1>
          <p>
            {{text[1].join(' ')}}
          </p>
        </div>
      </div>
      <div class="wrapper">
        <img src="../assets/fun/burst_pipe.jpeg" />
        <div class="text-content">
          <h1 class="sub-title">{{phrases[2]}}</h1>
          <p>
            {{text[2].join(' ')}}
          </p>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import faker from 'faker';
let phrases = [];
let paragraphs = [];
for (let i = 0; i < 3; i++) {
  phrases.push(faker.company.catchPhrase());
  let group = [];
  for (let j = 0; j < 8; j++) {
    group.push(faker.hacker.phrase())
  }
  paragraphs.push(group);
}

export default {
  name: 'About',
  data() {
    return {
      text: paragraphs,
      phrases: phrases
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.about {
  padding: 1rem;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.text-content h1 {
  text-align: center;
}

.text-content p {
  text-indent: 2rem;
  padding-top: 1rem;
  text-align: justify;
  border-top: 2px solid #00d1b2;
}

img {
  margin-bottom: 2rem;
  width: 450px;
  height: 450px;
}

@media(min-width: 900px) {
  .wrapper .text-content {
    margin: 0 4rem;
  }
}

@media(min-width: 769px) {
  .wrapper {
    flex-direction: row;
  }
  .wrapper:nth-child(2n) {
    flex-direction: row-reverse;
  }
  .wrapper .text-content {
    padding: 0.5rem 2rem;
    margin: 0 1rem;
  }
  .text-content p {
    text-indent: 2rem;
  }
  img {
    margin: 1rem;
  }
}
</style>



